import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, } from '@angular/common/http';
import { Observable, BehaviorSubject, of } from 'rxjs'
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

//const endpointAddress = 'http://54.164.151.252:5050/api/';
//const endpointAddress = 'http://192.168.1.9:3005/api/';
//const endpointAddress = 'http://localhost:3005/api/';

 const endpointAddress = 'api/';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-type': 'application/json',
  }),
};

@Injectable({
  providedIn: 'root'
})
export class ConfigService {

  public loginSucces: boolean = false;
  public user_list$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public user_list = this.user_list$.asObservable();
  public updateInUser$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public user_updateInUser = this.updateInUser$.asObservable();
  public updateInDepart$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public updateInDepart = this.updateInDepart$.asObservable();
  public departmentList$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public departmentList = this.departmentList$.asObservable();


  constructor(public http: HttpClient, private toastr: ToastrService, public router: Router) { }

  canActivate(): boolean {

    if (this.loginSucces == false) {
      this.router.navigate(['login']);
      return false;
    }
    return true;
  }
  login(request): Promise<any> {
    return this.http
      .post(endpointAddress + 'login', request, httpOptions)
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.loginSucces = true;
          this.router.navigate(['/dashboard'])
        }
        if (response.success == -1) {
          this.toastr.error(response.message)
        }
      })
      .catch(this.handleError<any>('login'))
  }
  getUser(): Promise<any> {
    return this.http
      .get(endpointAddress + 'getUserList')
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.user_list$.next(response.data)
        }
      })
      .catch(this.handleError<any>('user list'))
  }
  createUser(data): Promise<any> {
    return this.http
      .post(endpointAddress + 'createUser', data, httpOptions)
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.updateInUser$.next('update')
          this.toastr.success(response.message)
        }
        else {
          this.toastr.error(response.message)
        }
      })
      .catch(this.handleError<any>('create user'))
  }
  updateUser(request): Promise<any> {
    return this.http
      .patch(endpointAddress + 'updateUserInfo', request)
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.updateInUser$.next('update')
          this.toastr.success(response.message)
        }
        else {
          this.toastr.error(response.message)
        }
      })
      .catch(this.handleError<any>('update user'))
  }
  deleteUser(id): Promise<any> {
    const httpOption = {
      headers: new HttpHeaders().append('Content-type', 'application/json'),
      params: new HttpParams().append('_id', id)
    }
    return this.http
      .delete(endpointAddress + 'deleteUser', httpOption)
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.updateInUser$.next('update')
          this.toastr.success(response.message)
        }
        else {
          this.toastr.error(response.message)
        }
      })
      .catch(this.handleError<any>('delete user list'))
  }
  getDepartment(): Promise<any> {
    return this.http
      .get(endpointAddress + 'getDepartmentList')
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.departmentList$.next(response.data)
        }
      })
      .catch(this.handleError<any>('department list'))
  }
  saveDepartment(req): Promise<any> {
    return this.http
      .post(endpointAddress + 'createNewDepartment', req, httpOptions)
      .toPromise()
      .then((response: any) => {
        if (response.success == 0) {
          this.updateInDepart$.next('update')
          this.toastr.success(response.message)
        }
        else {
          this.toastr.error(response.message)
        }
      })
      .catch(this.handleError<any>('department list update'))
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error, operation);
      // this.toastr.error(error.error.message);
      console.log(`${operation} failed: ${error.error.message}`);
      return of(result as T);
    };
  }

}
