import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog, MAT_DIALOG_DEFAULT_OPTIONS } from '@angular/material';
import { ConfigService } from '../../service/config.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss'],
  // providers: [{ provide: MAT_DIALOG_DEFAULT_OPTIONS }]
})
export class AddComponent implements OnInit {

  addUserForm: FormGroup;
  user_data: any = { firstName: '', lastName: '', email: '', mobileNum: '', department: '' }
  modalType: any;
  user_data_modal: any;
  departmentList = [];
  newDepartment: any;

  constructor(private config: ConfigService, public dialog: MatDialog, public dialogRef: MatDialogRef<AddComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {

    this.addUserForm = new FormGroup({
      firstName: new FormControl('', [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(30)]),
      lastName: new FormControl('', [Validators.required, Validators.pattern('[a-zA-Z ]*'), Validators.minLength(2), Validators.maxLength(30)]),
      email: new FormControl('', [Validators.required]),
      mobileNum: new FormControl('', [Validators.required, Validators.pattern('[0-9]*'), Validators.minLength(10)]),
      department: new FormControl('', [Validators.required]),
      addDepartment: new FormControl('')
    })

    this.user_data_modal = Object.assign({}, this.user_data);
    this.modalType = data.type;

    if (this.modalType == 'edit' || this.modalType == 'delete') {
      this.user_data = Object.assign({}, data.data);
    }
    else {
      this.user_data = Object.assign({}, this.user_data_modal);
    }

    //get department list
    this.config.getDepartment().then()
    this.config.departmentList$.subscribe(list => {
      this.departmentList = list;
      console.log("depart", list)
    })
    this.config.updateInDepart$.subscribe(list => {
      this.config.getDepartment().then()
    })
  }

  ngOnInit() {
  }
  save() {
    this.config.createUser(this.user_data).then();
    this.modalClose()
  }
  update() {
    this.config.updateUser(this.user_data).then();
    this.modalClose()
  }
  delete() {
    this.config.deleteUser(this.user_data._id).then();
    this.modalClose()
  }
  clear() {
    this.dialogRef.close();
    this.modalClose()
  }
  modalClose() {
    this.dialogRef.close();
  }
  selectedDepart(depart) {
    // alert(depart)
  }
  saveDepartment() {
    let req = { department: this.newDepartment }
    this.config.saveDepartment(req).then();
    this.user_data.department = '';
    this.newDepartment = '';
  }
  cancleDepart() {
    this.user_data.department = this.user_data_modal.department;
    this.newDepartment = ''
  }
}
