import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AddComponent } from '../modal/add/add.component';
import { ConfigService } from '../service/config.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  user_list: any = []
  constructor(public dialog: MatDialog, private config: ConfigService) {

    //get user list
    this.config.getUser().then(data => {
    })
    //configure user list observable
    this.config.user_list$.subscribe(data => {
      this.user_list = data;
      console.log("list", data)
    })
    this.config.updateInUser$.subscribe(data => {
      this.config.getUser().then(data => { })
    })
  }

  ngOnInit() {
  }

  addUser(): void {
    const dialogRef = this.dialog.open(AddComponent, {
      width: '650px',
      data: { type: 'add', data: '' }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }
  editUser(data): void {
    const dialogRef = this.dialog.open(AddComponent, {
      width: '650px',
      data: { type: 'edit', data: data }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }
  deleteUser(data) {
    // let req = { _id: id }
    const dialogRef = this.dialog.open(AddComponent, {
      width: '450px',
      panelClass: 'delete-modalbox',

      data: { type: 'delete', data: data }
    });

  }

}
